import React, {useState} from "react"
import classes from "./Paginator.module.css";

const Paginator = React.memo(({totalUsersCount, usersOnPage, startPage, setStartPage, activePage, myMethod, portionOfPage, SetPortionOfPage}) => {
        let pageCounter = Math.ceil(totalUsersCount / usersOnPage)
        let page = [];
        const [pagePortion, setpagePortion] = useState(null);

        pageCounter = startPage + Number(portionOfPage)

        if (startPage < 1) {startPage = 1}

        for (let i = startPage; i < pageCounter; i++) {
            page.push(i);
            var lastPage = i;
        }

        let decreationValue = Number(portionOfPage)

        if (startPage / Number(portionOfPage) < 1) {
            decreationValue = lastPage - Number(portionOfPage)
        }

        let handleChange = (event) => {
            setpagePortion(event.target.value)
        }

        let setSpecialPortionOfPage = (event) => {
            SetPortionOfPage(pagePortion)
            event.preventDefault();
        }
        console.log("------------------------------------------------------------------")
        return (
            <div className={classes.pagination}>
                <div>
                    <form onSubmit={setSpecialPortionOfPage}>
                        <div>
                            <label>Input page portion: </label>
                            <input type="number" defaultValue={pagePortion} onChange={handleChange}
                                   placeholder="page portion" max="50"/>
                        </div>
                        <div>
                            <input type="submit" value="Add" className={classes.button}/>
                        </div>
                    </form>
                </div>

                {startPage >= (Number(portionOfPage))
                    ? <span onClick={() => {
                        setStartPage(lastPage - (Number(portionOfPage) * 2 - 1))
                    }} className={classes.pageNumber + " " + classes.ahead}>-{Number(portionOfPage)}...</span>
                    : startPage === 1 ? null : <span onClick={() => {
                        setStartPage(lastPage - (Number(portionOfPage) * 2 - 1))
                    }} className={classes.pageNumber + " " + classes.ahead}>...-{decreationValue}</span>}

                {page.map(p => activePage === p
                    ? <span key={p} onClick={() => {
                        myMethod(p);
                    }} className={classes.pageNumber + " " + classes.selected}>{p}</span>
                    : <span key={p} onClick={() => {
                        myMethod(p);
                    }} className={classes.pageNumber}>{p}</span>)}

                <span onClick={() => {
                    setStartPage(lastPage + 1)
                }} className={classes.pageNumber + " " + classes.ahead}>...+{Number(portionOfPage)}</span>
            </div>
        )
    }
)
export default Paginator